.cartDropdown {
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 50px;
    right: 12px;
    z-index: 5;
  
    .cartItems {
      height: 240px;
      display: flex;
      flex-direction: column;
      overflow: scroll;

      .emptyMessage {
        font-size: 18px; 
        margin: 50px auto; 
      }

    }
  
    button {
      margin-top: auto;
    }
  }
  